/**
 * Hide an element visually while keeping it exposed to assistive technology.
 *
 * 1. Hide content by "clipping" it away entirely (including borders). Note that
 *    `clip` is deprecated, but the new `clip-path` has less browser support, so
 *    we'll use both.
 * 2. Most screen readers will skip elements less than 1px by 1px.
 * 3. Remove element from the document flow so that it does not take up a 1px
 *    space (which even if imperceptible in most instances can still cause
 *    otherwise nonexistent scrollbars in the containing scrollable area).
 */
/**
 * This color system uses the CIELUV color space to maintain perceptual
 * uniformity of luminance. The colors were generated using the HSLuv tool,
 * based on the primary brand color as a starting point (#da2530), which in
 * CIELUV represented as HSL is H 11, S 87.7%, L 47.6%. Thus, all colors have
 * S 87.7%, all reds have H 11, and all medium/default colors have L 47.6%.
 *
 * The rest of the colors were generated accordingly, using these hues: red: 11,
 * blue: 250, yellow: 78.8, green: 135, gray: 265.9.
 *
 * Tints and shades were generated in increments of roughly 10%–20%, and indexed
 * on a scale of 0 to 500 in increments of 100: 0: 97.5% (~100%), 100: 90%, 200:
 * 70%, 300: 47.6% (~50%), 400: 30%, 500: 10%.
 *
 * A difference between two grades of 300 or greater means those two colors are
 * accessible together. This is the "magic number": 300 is both an accessible
 * difference between two grades, as well as the "medium" grade.
 *
 * Lastly, for each color, a corresponding preferred foreground color and link
 * color is specified.
 */
/**
 * The colors.
 */
/**
 * Although colors are defined above as basic variables, we'll also create a
 * map, that way we can iterate over them, use getter functions, etc. The reason
 * we don't just put the values directly in the map is because many values need
 * to be referenced more than once within the map.
 */
/**
 * Semantic aliases.
 *
 * Semantic colors are named according to their purpose, role, or function,
 * rather than by their hue. It is preferred that colors are referenced in the
 * codebase by their semantic aliases in most cases.
 *
 * There are two categories of semantics: theme aliases and interface aliases.
 * Theme aliases refer to their color's purpose in the theme (e.g. primary,
 * secondary, etc.) Interface aliases refer to their color's deeper contextual
 * meaning, as they relate to the content they're paired with (e.g. positive,
 * negative, etc.)
 *
 * Elements that should be themeable should use theme aliases, such as the
 * site's header, link colors, logo backgrounds, etc. Elements that convey a
 * contextual meaning should use interface aliases, such as error messages,
 * notifications, system warnings, etc.
 */
/**
 * This file serves as the entrypoint for importing (via `@use`) all the index
 * files of each folder. Note that the actual file names imported here are
 * omitted, as if they are not specified, they default to _index.scss. So
 * `@forward "base"` will automatically import base/_index.scss.
 *
 * Index files only forward abstracts (variables, mixins, and functions). By
 * importing this file, all available abstracts from each folder are forwarded
 * and ready to be used. This file does not import any actual CSS rules. That is
 * the responsibility of tcds.scss.
 *
 * To use this file in individual stylesheets inside of subfolders, you do not
 * need to specify the file name. Simply use:
 *   @use "../" as *;
 *
 * Or, if you wish to namespace everything to avoid collisions:
 *   @use "../" as tcds;
 *
 * Within the TCDS codebase, however, this is not necessary.
 */
:target {
  outline: 2px solid transparent;
  animation: highlight-target 7s ease-out;
}
@keyframes highlight-target {
  5%, 50% {
    background-color: #fff9d4;
    color: #434800;
    outline-color: #f5e550;
  }
}

/**
 * This file contains many styles traditionally put in "reset" or "normalize"
 * files. While this file does replace those, its goal is not (necessarily) to
 * wipe out browser defaults or eliminate differences between them. Instead, it
 * is to provide universal styles that improve UX and serve as a guardrail for
 * things like accessibility, maintainability, and responsiveness.
 *
 * In this project, resets and normalizations may instead be found in their
 * respective locations, such as removing `list-style` from lists in
 * ../primitives/_lists.
 *
 * Rules for adding to this stylesheet:
 * 1. Contains as few styles as possible, with no styles that are likely to be
 *    overridden on a project-by-project basis.
 * 2. Contains no project-specific defaults, globals, utilities, or resets.
 *    Should only contain styles that apply universally.
 * 2. Introduces as few side effects on individual components or other modular
 *    elements as possible; they should generally still look and function the
 *    same with or without this stylesheet. (As a result, this file should not
 *    be treated as a "dependency", but as a progressive enhancement.)
 * 3. Has no focus on browser compatibility issues (but may still include
 *    vendor-specific styles if applicable).
 */
/**
 * 1. Better default box model (`padding` will now not add to an element's
 *    calculated dimensions, but will instead push content inwards).
 */
* {
  box-sizing: border-box;
}
*::before, *::after {
  box-sizing: border-box;
}

/**
 * 1. Animate scrolling to anchor elements to help users better understand the
 *    direction and relative location of the target element (only if "reduced
 *    motion" preference is not set).
 */
@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
  }
}

/**
 * 1. Remove default viewport margin.
 * 2. Prevent horizontal overflow and scrolling of viewport.
 * 3. Prevent automatic text scaling on orientation change. Both a design and
 *    performance consideration; changes to `font-size` trigger reflow.
 */
body {
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
  text-size-adjust: 100%;
}

/**
 * 1. Remove touch delay on most interactive elements. This delay is an
 *    affordance for double-tapping to zoom in, but is unnecessary for (and can
 *    cause confusion on) elements where deliberate interaction is presumable
 *    when double-tapped. (Note that `manipulation` still affords
 *    touch-then-scrolling interaction.)
 */
a,
button,
input,
select,
summary {
  touch-action: manipulation;
}

/**
 * 1. Prevent media from expanding beyond the natural size of the parent.
 * 2. Remove excess bottom space left for descenders on inline elements.
 */
audio,
canvas,
iframe,
img,
svg,
video,
button {
  max-width: 100%;
  vertical-align: middle;
}

/**
 * 1. Strengthen the `[hidden]` attribute so that it overrides `display`
 *    declarations in other rulesets or inline styles.
 */
[hidden] {
  display: none !important;
}

/**
 * Hide an element visually while keeping it exposed to assistive technology
 * (and focusable for keyboard navigation).
 *
 * 1. Do not apply styles if the element is focused or active (for instance if 
 *    clicked after focusing).
 */
.visually-hidden:not(:focus):not(:active) {
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  position: absolute;
}

