body {
  color: #464652;
  font-family: Inter, system-ui, sans-serif;
  font-size: 1rem;
  line-height: 1.75;
}

h2,
h3,
h4,
h5,
h6 {
  line-height: 1.15;
  margin: 1rem 0;
}

:not(h1) + h2,
:not(h2) + h3,
:not(h3) + h4 {
  margin-top: 3.5rem;
}

h1 {
  color: #2d73bc;
  font-weight: 700;
  letter-spacing: -0.025em;
  line-height: 1;
  margin: 0 0 2rem;
}
h1,
.text-xx-large {
  font-size: 2.5rem;
}

h2 {
  font-weight: 400;
}
h2,
.text-x-large {
  font-size: 2rem;
}

h3 {
  font-weight: 400;
}
h3,
.text-large {
  font-size: 1.5rem;
}

h4 {
  font-weight: 700;
}
h4,
.text-medium {
  font-size: 1rem;
}

h5 {
  font-weight: 700;
}
h5,
.text-small {
  font-size: 0.875rem;
}

.text-x-small {
  font-size: 0.75rem;
}

small,
sub,
sup {
  font-size: 87.5%;
  font-size: max(0.875rem, 87.5%);
}

b,
strong {
  font-weight: 700;
}

a {
  color: #2d73bc;
  text-decoration: underline;
}
a:where(:visited) {
  color: #004880;
}

p {
  margin: 0;
}
p:where(:not(:last-child)) {
  margin-bottom: 1em;
}

hr {
  border: 0;
  border-top: 4px solid #e2e2e7;
  margin: 0 0 2rem;
}

:where(ul[class]:not(.ul),
  ol[class]:not(.ol),
  dl[class]:not(.dl)) {
  list-style: none;
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
}

main {
  counter-reset: footnotes;
}

.Footnotes {
  font-size: 0.875rem;
  list-style: decimal;
  margin-left: 16px;
}
.Footnotes:empty {
  display: none;
}

.Footnote-ref {
  counter-increment: footnotes;
  font-size: 0.75rem;
  position: relative;
  top: -0.5em;
}
.Footnote-ref::after {
  content: "[" counter(footnotes) "]";
}

.Icon {
  display: inline-flex;
  height: 1em;
  vertical-align: middle;
}
.Icon svg {
  height: 100%;
  width: 100%;
}

.Tabs--small {
  --tab-font-size: 0.875rem;
  --tab-height: 32px;
  --tab-x-padding: 16px;
}
.Tabs--large {
  --tab-flex-grow: 1;
  --tab-font-size: 1.25rem;
  --tab-height: 48px;
  --tab-x-padding: 32px;
  --tabpanel-y-padding: 24px;
}
.Tabs__tablist {
  display: flex;
}
.Tabs__tab {
  appearance: none;
  background: none;
  border: none;
  border-bottom: 5px solid var(--indicator-color, #e2e2e7);
  color: #707079;
  font-family: Inter, system-ui, sans-serif;
  font-size: var(--tab-font-size, 1rem);
  font-weight: 600;
  flex: var(--tab-flex-grow, 0);
  padding: 0 24px;
  padding: 0 var(--tab-x-padding, 24px);
  height: var(--tab-height, 40px);
  white-space: nowrap;
  transition: border-color 0.15s ease-in, color 0.15s ease-in;
}
@media (any-hover: hover) {
  .Tabs__tab:hover {
    --indicator-color: #aaaab3;
    color: #464652;
  }
}
@media (hover: none) {
  .Tabs__tab:active {
    opacity: 0.5;
  }
}
.Tabs__tab[aria-selected=true] {
  --indicator-color: #2d73bc;
  color: #1b1b1e;
}
.Tabs__panel {
  padding: var(--tabpanel-y-padding, 16px) 0 0;
}

.Accordion__controls {
  display: flex;
  justify-content: end;
  list-style: none;
  margin: 0;
}
.Accordion__section {
  margin-bottom: 3px;
}
.Accordion__button {
  appearance: none;
  border: 0;
  background: #f8f8f9;
  color: #464652;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  font-family: Inter, system-ui, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  padding: 16px 24px;
  width: 100%;
  transition: background 0.15s ease-in, color 0.15s ease-in;
}
.Accordion__button:hover {
  background: #2d73bc;
  color: #f5f8ff;
}
.Accordion__button[aria-expanded=true] {
  --icon-rotate: 180deg;
}
.Accordion__icon {
  width: 1em;
  pointer-events: none;
  transform: rotateZ(var(--icon-rotate, 0));
  transition: transform 0.15s ease-in;
}
.Accordion__panel {
  overflow: hidden;
  transition: height 0.8s cubic-bezier(0, 1, 0.4, 1);
}
.Accordion__content {
  padding: 16px;
}

.Button {
  --button-height: 2.5rem;
  --button-font-size: 1rem;
  --button-padding: calc((var(--button-height) - var(--button-font-size)) / 1.5);
  appearance: none;
  display: inline-flex;
  align-items: center;
  background: #da2530;
  color: #fff6f6;
  border: 0;
  font: inherit;
  font-size: var(--button-font-size);
  height: var(--button-height);
  line-height: 1;
  white-space: nowrap;
  padding: 0 var(--button-padding);
  overflow: hidden;
  position: relative;
  text-decoration: none;
  transform: scale(var(--button-scale, 1)) translate(var(--button-translate, 0, 0));
  transition: transform 0.15s ease-in;
  user-select: none;
  z-index: 1;
  /** States */
  /** Modifiers */
  /** Children */
}
.Button:hover {
  --button-overlay-color: rgba(0, 0, 0, 0.1);
}
.Button:active {
  --button-overlay-color: rgba(0, 0, 0, 0.3);
  --button-scale: .99;
}
.Button--small {
  --button-font-size: 0.875rem;
}
@media (pointer: fine) {
  .Button--small {
    --button-height: 2rem;
  }
}
.Button--large {
  --button-height: 3rem;
  --button-font-size: 1.25rem;
}
.Button--full-width {
  display: flex;
  justify-content: center;
  width: 100%;
}
.Button--round {
  border-radius: var(--button-height);
}
.Button--ghost {
  background: transparent;
  color: inherit;
  --button-icon-background: transparent;
}
.Button--ghost.Button--icon-right {
  --button-icon-margin-left: 0;
}
.Button--ghost:not(.Button--icon-right) {
  --button-icon-margin-right: 0;
}
.Button--icon-right {
  flex-direction: row-reverse;
  --button-icon-margin-right: calc(var(--button-padding) * -1);
  --button-icon-margin-left: var(--button-padding);
}
.Button--icon-only {
  padding: 0;
  --button-icon-background: transparent;
  --button-icon-margin-right: 0;
  --button-icon-margin-left: 0;
}
.Button::after {
  content: "";
  background: var(--button-overlay-color, transparent);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: background 0.15s ease-in;
  z-index: -1;
}
.Button svg {
  background: var(--button-icon-background, rgba(0, 0, 0, 0.1));
  height: 100%;
  margin: 0 var(--button-icon-margin-right, var(--button-padding)) 0 var(--button-icon-margin-left, calc(var(--button-padding) * -1));
  padding: calc(var(--button-padding) * 0.65);
}

